
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent, ref } from "vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { IonPage } from "@ionic/vue";
import { HtsPregnancyService } from "@/apps/HTS/services/hts_pregnancy_status_service";
import HisStandardForm from "@/components/Forms/HisStandardForm.vue";
import { StartHtsEncounter } from "@/apps/HTS/composables/HtsFormEncounter";

export default defineComponent({
  components: { IonPage, HisStandardForm },
  setup() {
    const fields = ref<Field[]>([])
    const service = new HtsPregnancyService(-1)

    const { saveEncounter, patientDashboardUrl } = StartHtsEncounter({
      onInitVisit: () => {
        fields.value = [
          {
            id: "hts_female_pregnant",
            helpText: "Select pregnancy status",
            type: FieldType.TT_SELECT,
            isRequired: () => true,
            finalComputedValue: (v: Option) => {
              const concept = "Pregnancy status"
              return {
                offlineMeta: {
                  [concept]: v.value
                },
                obs: service.buildValueCoded(concept, v.value),
              };
            },
            options: () => {
              return [
                { label: "Non-pregnant", value: "Not Pregnant / Breastfeeding" },
                { label: "Pregnant", value: "Pregnant woman" },
                { label: "Breastfeeding", value: "Breastfeeding" },
              ];
            }
          }
        ]
      }
    })
    
    function onFinish(_: any, computedData: any) {
      saveEncounter({ 
        encounterName: "PREGNANCY STATUS",
        encounterTypeID: service.encounterTypeID, 
        computedData 
      })
    }

    return {
      patientDashboardUrl,
      onFinish,
      fields
    }
  }
});
